@keyframes fade {
    0% {
        display: none;
        opacity: 0; }


    1% {
        display: block; }


    100% {
        opacity: 1; } }



@keyframes fadeOut {
    70% {
        opacity: 1; }


    100% {
        opacity: 0;
        display: none;
        z-index: -1; } }



@keyframes fadeInOut {
    0% {
        display: none;
        opacity: 0; }


    1% {
        display: block; }


    50% {
        opacity: 1; }


    99% {
        opacity: 0; }


    100% {
        display: none; } }

